
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ManageTariffCrudComponent } from './manage-tariff-crud/manage-tariff-crud.component';
import { TABS_DEMO_ROUTES } from './manage-tariff-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : ManageTariffCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class ManageTariffRoutingModule{}